<script>
const TabView = {
  data() {
    return {
      active: "1",
      timer: null,
      t: 0
    };
  },
  render() {
    return (
      <div
        class="tab-view"
        style={{ padding: "20px" }}
        on-mouseenter={this.mouseenter}
        on-mouseleave={this.mouseleave}
      >
        <el-tabs tab-position="bottom" value={this.active} on-tab-click={ ({ name }) => this.dispatchEvent(name) }>
          <el-tab-pane label="用户管理" name="1">
            用户管理
          </el-tab-pane>
          <el-tab-pane label="配置管理" name="2">
            配置管理
          </el-tab-pane>
        </el-tabs>
      </div>
    );
  },
  methods: {
    mouseenter(ev) {
      clearInterval(this.timer);
    },
    mouseleave(ev) {
      this.setTimer()
    },
    setTimer() {
      this.timer = setInterval(() => {
        this.t++;
        this.setActive(String((this.t % 2) + 1));
      }, 2000);
    },
    setActive(name) {
        this.active = name;
        this.dispatchEvent(name);
    },
    dispatchEvent(value) {
        this.$emit('change', value);
    }
  },
  mounted() {
    this.setTimer()
  }
};

const DataView = {
  props: ["index"],
  render() {
    const { index } = this.$props;
    return (
      <div class="data-view" style={{ padding: "20px" }}>
        {index}
      </div>
    );
  },
  watch: {
    index(val) {
      console.log(val);
    }
  }
};

export default {
    data() {
        return {
            index: 1
        }
    },
  render() {
    return (
      <div class="test">
        <el-row gutter={20}>
          <el-col span={12}>
            <TabView on-change={ ev => this.index = ev }/>
          </el-col>
          <el-col span={12}>
            <DataView index={ this.index } />
          </el-col>
        </el-row>
      </div>
    );
  }
};
</script>